import { Card } from "./card";
import BarChartCard from "./BarChartCard";

const barData = [
    { month: "Jan", value: 400 },
    { month: "Feb", value: 300 },
    { month: "Mar", value: 200 },
    { month: "Apr", value: 278 },
    { month: "May", value: 189 },
];

const chartData = [
    { month: "January", desktop: 186 },
    { month: "February", desktop: 305 },
    { month: "March", desktop: 237 },
    { month: "April", desktop: 73 },
    { month: "May", desktop: 209 },
    { month: "June", desktop: 214 },
];

const barConfig = {
    value: {
        label: "Value",
        color: "hsl(var(--chart-1))",
    },
};

const chartConfig = {
    desktop: {
        label: "Desktop",
        color: "hsl(var(--chart-1))",
    },
};

export default function DashboardCharts() {
    return (
        <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6 w-full">
            {/* Bar Chart */}
            <BarChartCard
                data={barData}
                config={barConfig}
                barKey="value"
                title="Revenue Growth"
                description="Month-over-month increase in total platform revenue"
                summary="25% growth over last month"
            />
            {/* Area Chart */}
            <Card className="p-4 flex flex-col items-center">
                <div className="text-lg font-semibold mb-2">User Engagement</div>
                <div className="w-full h-48 bg-accent/20 rounded flex items-center justify-center text-accent">
                    Area Chart
                </div>
            </Card>
            {/* Line Chart */}
            <Card className="p-4 flex flex-col items-center">
                <div className="text-lg font-semibold mb-2">Daily Active Users</div>
                <div className="w-full h-48 bg-accent/20 rounded flex items-center justify-center text-accent">
                    Line Chart
                </div>
            </Card>
            {/* Conversion Rate Chart */}
            <Card className="p-4 flex flex-col items-center">
                <div className="text-lg font-semibold mb-2">Conversion Rate</div>
                <div className="w-full h-48 bg-accent/20 rounded flex items-center justify-center text-accent">
                    Conversion Rate Chart
                </div>
            </Card>
            {/* Monthly Sales Chart */}
            <Card className="p-4 flex flex-col items-center">
                <div className="text-lg font-semibold mb-2">Monthly Sales</div>
                <div className="w-full h-48 bg-accent/20 rounded flex items-center justify-center text-accent">
                    Monthly Sales Chart
                </div>
            </Card>
            {/* New Bar Chart */}
            <BarChartCard
                data={chartData}
                config={chartConfig}
                title="Bar Chart"
                description="January - June 2024"
                summary="Trending up by 5.2% this month"
                barKey="desktop"
            />
        </div>
    );
} 